import { QueuedHttpClient } from '@buydy/cl-http-priority-queue-client';

// API Base URL
const API_BASE_URL = 'http://localhost:3001/api/v1';

// Custom API Error class
class ApiError extends Error {
  constructor(message, status, details) {
    super(message);
    this.name = 'ApiError';
    this.status = status;
    this.details = details;
  }
}

// Create global QueuedHttpClient instance with optimized settings
const apiClient = new QueuedHttpClient({
  baseURL: API_BASE_URL,
  timeout: 30000,
  maxConcurrency: 6, // Max 6 simultaneous requests (browser default)
  name: 'Stocks API',

  // Memory cache (fast, short-lived)
  enableMemoryCache: true,
  defaultMemoryCacheTTL: 5 * 60 * 1000, // 5 minutes

  // LocalStorage cache (persistent, long-lived - reduces EODHD API calls)
  enableLocalStorageCache: true,
  defaultLocalStorageCacheTTL: 60 * 60 * 1000, // 1 hour (reduces EODHD requests)
  localStorageCacheMaxSizeMB: 5, // 5MB limit
  localStorageCacheMaxEntries: 500, // Max 500 cached responses

  // Enable request deduplication
  enableDeduplication: true,

  // Enable automatic retry with exponential backoff
  enableRetry: true,
  maxRetries: 3,
  retryDelay: 1000,

  // Request lifecycle callbacks
  onProgress: (completed, total) => {
    console.log(`[API] Progress: ${completed}/${total} requests completed`);
  },
  onRequestComplete: (_result, _index) => {
    const stats = apiClient.getStats();
    if (stats.totalRequests % 10 === 0) {
      // Log stats every 10 requests
      console.log('[API] Stats:', stats);
    }
  },
  onRequestError: (error, _index) => {
    console.error(`[API] Request failed:`, error.message);
  },
  onCacheFull: () => {
    console.warn('[API] LocalStorage cache full, evicting old entries');
  },
});

// Export the client for direct access if needed
export { apiClient };

/**
 * Helper function to handle API responses and errors
 */
async function handleApiRequest(requestPromise) {
  try {
    return await requestPromise;
  } catch (error) {
    // Handle axios errors
    if (error.response) {
      // Server responded with error status
      throw new ApiError(
        error.response.data?.error || `HTTP ${error.response.status}`,
        error.response.status,
        error.response.data,
      );
    } else if (error.request) {
      // Request was made but no response received
      throw new ApiError('Network error or server unavailable', 0, {
        originalError: error.message,
      });
    } else {
      // Something else happened
      throw new ApiError(error.message, 0, { originalError: error.message });
    }
  }
}

// Jobs API
export const jobsApi = {
  // Get all jobs with filtering and pagination
  getAll: async (params = {}) => {
    return handleApiRequest(
      apiClient.get('/jobs', {
        params,
        priority: 50, // Normal priority
        cache: true,
        cacheTTL: 30000, // Cache for 30 seconds
      }),
    );
  },

  // Get job by ID
  getById: async (id) => {
    return handleApiRequest(
      apiClient.get(`/jobs/${id}`, {
        priority: 10, // High priority for single job view
        cache: true,
        cacheTTL: 15000, // Cache for 15 seconds
      }),
    );
  },

  // Create new job
  create: async (jobData) => {
    return handleApiRequest(
      apiClient.post('/jobs', jobData, {
        priority: 5, // High priority for user actions
        cache: false,
      }),
    );
  },

  // Update job
  update: async (id, updateData) => {
    return handleApiRequest(
      apiClient.put(`/jobs/${id}`, updateData, {
        priority: 5, // High priority for user actions
        cache: false,
      }),
    );
  },

  // Delete job
  delete: async (id) => {
    return handleApiRequest(
      apiClient.delete(`/jobs/${id}`, {
        priority: 5, // High priority for user actions
        cache: false,
      }),
    );
  },

  // Run job (create new job with same name and trigger execution)
  runJob: async (id) => {
    return handleApiRequest(
      apiClient.post(`/jobs/${id}/run`, null, {
        priority: 1, // Critical priority for job execution
        cache: false,
        retry: true,
        maxRetries: 2,
      }),
    );
  },

  // Get job statistics
  getStats: async () => {
    return handleApiRequest(
      apiClient.get('/jobs/stats', {
        priority: 20, // Medium-high priority
        cache: true,
        cacheTTL: 60000, // Cache for 1 minute
      }),
    );
  },

  // Get recent jobs
  getRecent: async (limit = 20) => {
    return handleApiRequest(
      apiClient.get('/jobs/recent', {
        params: { limit },
        priority: 30,
        cache: true,
        cacheTTL: 20000, // Cache for 20 seconds
      }),
    );
  },

  // Get running jobs
  getRunning: async () => {
    return handleApiRequest(
      apiClient.get('/jobs/running', {
        priority: 10, // High priority for real-time data
        cache: true,
        cacheTTL: 5000, // Cache for 5 seconds only
      }),
    );
  },

  // Get failed jobs
  getFailed: async (since = null) => {
    return handleApiRequest(
      apiClient.get('/jobs/failed', {
        params: since ? { since } : {},
        priority: 40,
        cache: true,
        cacheTTL: 30000,
      }),
    );
  },

  // Get job history
  getHistory: async (name, limit = 20) => {
    return handleApiRequest(
      apiClient.get(`/jobs/history/${encodeURIComponent(name)}`, {
        params: { limit },
        priority: 50,
        cache: true,
        cacheTTL: 45000,
      }),
    );
  },

  // Get jobs grouped by type/name
  getJobsByType: async (params = {}) => {
    return handleApiRequest(
      apiClient.get('/jobs/types', {
        params,
        priority: 50,
        cache: true,
        cacheTTL: 60000,
      }),
    );
  },
};

// Stocks API
export const stocksApi = {
  // Get heatmap data
  getHeatmap: async (params = {}) => {
    return handleApiRequest(
      apiClient.get('/stocks/heatmap', {
        params,
        priority: 1, // Critical priority for main feature
        cache: true,
        cacheTTL: 300000, // Cache for 5 minutes
      }),
    );
  },

  // Get stock details
  getDetails: async (symbol) => {
    return handleApiRequest(
      apiClient.get(`/stocks/${symbol}`, {
        priority: 10,
        cache: true,
        cacheTTL: 120000, // Cache for 2 minutes
      }),
    );
  },

  // Search stocks
  search: async (query) => {
    return handleApiRequest(
      apiClient.get('/stocks/search', {
        params: { q: query },
        priority: 5, // High priority for user search
        cache: true,
        cacheTTL: 300000, // Cache for 5 minutes
      }),
    );
  },
};

// Health check
export const healthApi = {
  check: async () => {
    return handleApiRequest(
      apiClient.get('/health', {
        priority: 100, // Low priority
        cache: false,
        retry: false, // Don't retry health checks
      }),
    );
  },
};

// Utility functions
export const apiUtils = {
  // Get current API statistics (includes localStorage cache stats)
  getStats: () => {
    return apiClient.getStats();
  },

  // Clear memory cache only
  clearMemoryCache: (pattern) => {
    apiClient.clearMemoryCache(pattern);
  },

  // Clear localStorage cache only
  clearLocalStorageCache: (pattern) => {
    apiClient.clearLocalStorageCache(pattern);
  },

  // Clear all caches (memory + localStorage)
  clearAllCaches: (pattern) => {
    apiClient.clearAllCaches(pattern);
  },

  // Legacy method for backward compatibility
  clearCache: (pattern) => {
    apiClient.clearAllCaches(pattern);
  },

  // Wait for all pending requests to complete
  waitForCompletion: async () => {
    return apiClient.waitForCompletion();
  },

  // Cancel all pending requests
  cancelAll: () => {
    apiClient.cancel();
  },
};

export { ApiError };
