const Table = ({
  columns = [],
  data = [],
  getCellClassName,
  getCellValue,
  onCellClick,
  stickyFirstColumn = false,
  className = '',
}) => {
  return (
    <div className={`bg-white rounded-lg shadow overflow-x-auto ${className}`}>
      <table className="min-w-full">
        <thead className="bg-gray-100">
          <tr>
            {columns.map((column, index) => (
              <th
                key={column.key || index}
                className={`px-4 py-3 text-${column.align || 'left'} text-xs font-medium text-gray-700 uppercase ${
                  stickyFirstColumn && index === 0
                    ? 'sticky left-0 bg-gray-100'
                    : ''
                }`}
              >
                {column.label}
              </th>
            ))}
          </tr>
        </thead>
        <tbody className="divide-y divide-gray-200">
          {data.map((row, rowIndex) => (
            <tr key={row.id || rowIndex} className="hover:bg-gray-50">
              {columns.map((column, colIndex) => {
                const value = getCellValue
                  ? getCellValue(row, column)
                  : row[column.key];
                const cellClassName = getCellClassName
                  ? getCellClassName(row, column, value)
                  : '';
                const isFirstColumn = stickyFirstColumn && colIndex === 0;

                return (
                  <td
                    key={column.key || colIndex}
                    className={`px-4 py-3 text-${column.align || 'left'} text-sm ${cellClassName} ${
                      isFirstColumn
                        ? 'font-medium text-gray-900 sticky left-0 bg-white'
                        : ''
                    }`}
                    onClick={() =>
                      onCellClick && onCellClick(row, column, value)
                    }
                    title={
                      column.getTooltip
                        ? column.getTooltip(row, value)
                        : undefined
                    }
                  >
                    {column.render ? column.render(value, row) : value}
                  </td>
                );
              })}
            </tr>
          ))}
        </tbody>
      </table>
      {data.length === 0 && (
        <div className="text-center text-gray-500 py-8">No data available</div>
      )}
    </div>
  );
};

export default Table;
