import { useState, useEffect } from 'react';
import {
  fetchHeatmapData,
  rankCompanies,
  fetchSectors,
  fetchIndustries,
  fetchAvailableMetrics,
  exportToCSV,
} from '../services/heatmapService';
import {
  loadPriceRangeData,
  applyPriceRangeFilters,
} from '../services/priceDataService';
import { CheckboxList } from '@buydy/cl-checkbox-list';
import { DualSlider, Slider } from '@buydy/cl-slider';
import { Select } from '@buydy/cl-select';
import { Button } from '@buydy/cl-button';
import { Table } from '@buydy/cl-table';

function HeatMap() {
  // Format value based on metric type
  const formatMetricValue = (value, metricType) => {
    if (value === null || value === undefined || isNaN(value)) {
      return 'N/A';
    }

    switch (metricType) {
      case 'currency': {
        // Format large currency values with K, M, B suffixes
        const absValue = Math.abs(value);
        if (absValue >= 1e9) {
          return `$${(value / 1e9).toFixed(2)}B`;
        } else if (absValue >= 1e6) {
          return `$${(value / 1e6).toFixed(2)}M`;
        } else if (absValue >= 1e3) {
          return `$${(value / 1e3).toFixed(2)}K`;
        }
        return `$${value.toFixed(0)}`;
      }

      case 'ratio':
        // Format ratio as number with 2 decimals
        return value.toFixed(2);

      case 'percentage':
        // Format percentage with % symbol
        return `${(value * 100).toFixed(2)}%`;

      default:
        return value.toFixed(2);
    }
  };

  // Color utility for percentile gradient
  const getPercentileColor = (percentile, threshold = 0) => {
    if (percentile === null || percentile === undefined || isNaN(percentile)) {
      return 'bg-gray-100';
    }

    // If percentile is below threshold, show gray to indicate it's not playing in scoring
    if (percentile < threshold) {
      return 'bg-gray-300';
    }

    // Red  Yellow  Green gradient for values above threshold
    if (percentile >= 0.8) return 'bg-green-500';
    if (percentile >= 0.6) return 'bg-green-300';
    if (percentile >= 0.4) return 'bg-yellow-300';
    if (percentile >= 0.2) return 'bg-orange-300';
    return 'bg-red-300';
  };

  const getTextColorForBg = (percentile) => {
    if (percentile >= 0.6) return 'text-white';
    return 'text-gray-900';
  };
  // State
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);

  // Filters
  const [sectors, setSectors] = useState([]);
  const [industries, setIndustries] = useState([]);
  const [availableMetrics, setAvailableMetrics] = useState([]);

  const [selectedGroupBy, setSelectedGroupBy] = useState('sector');
  const [selectedSectors, setSelectedSectors] = useState([]);
  const [selectedIndustries, setSelectedIndustries] = useState([]);
  const [selectedMetrics, setSelectedMetrics] = useState([
    'DividendYieldCurrent',
    'DividendGrowth5Y',
    'DebtToEquityCurrent',
    'EBITDAGrowth1Y',
  ]);

  // Heatmap data
  const [heatmapData, setHeatmapData] = useState(null);

  // Price range data
  const [priceData, setPriceData] = useState(null);
  const [isLoadingPriceData, setIsLoadingPriceData] = useState(false);
  const [priceDataError, setPriceDataError] = useState(null);

  // Formula
  const [formulaMethod, setFormulaMethod] = useState('avg'); // 'avg' | 'weighted' | 'count'
  const [threshold, setThreshold] = useState(0.0);
  const [weights, setWeights] = useState({});

  // Price Range Filters
  const [priceRangeFilters, setPriceRangeFilters] = useState({
    '1D': [0, 100],
    '1W': [0, 100],
    '1M': [0, 100],
    '3M': [0, 100],
    '1Y': [0, 100],
  });

  // Debouncing for real-time filtering
  const [filterTimeout, setFilterTimeout] = useState(null);
  const [isFiltering, setIsFiltering] = useState(false);

  // Ranked results
  const [rankedCompanies, setRankedCompanies] = useState([]);

  // Load initial data
  useEffect(() => {
    console.log(' HeatMap component mounted, loading initial data...');
    loadInitialData();
  }, []);

  // Cleanup timeout on unmount
  useEffect(() => {
    return () => {
      if (filterTimeout) {
        clearTimeout(filterTimeout);
      }
    };
  }, [filterTimeout]);

  // Load industries when sectors change
  useEffect(() => {
    if (selectedSectors.length > 0) {
      loadIndustriesForSectors(selectedSectors);
    } else {
      loadAllIndustries();
    }
  }, [selectedSectors]);

  // Reset filters when groupBy changes
  useEffect(() => {
    // Clear all filters when groupBy changes
    setSelectedSectors([]);
    setSelectedIndustries([]);
    setIndustries([]);

    // Load appropriate data based on groupBy selection
    if (selectedGroupBy === 'industry') {
      loadAllIndustries();
    }
  }, [selectedGroupBy]);

  async function loadInitialData() {
    console.log(' Loading initial data...');
    try {
      const [sectorsData, metricsData] = await Promise.all([
        fetchSectors(),
        fetchAvailableMetrics(),
      ]);

      console.log(' Initial data loaded:', {
        sectors: sectorsData.length,
        metrics: metricsData.length,
      });

      setSectors(sectorsData);
      setAvailableMetrics(metricsData);

      // Set default selections to all options
      setSelectedSectors(sectorsData);
      setSelectedIndustries([]); // Will be set when industries load

      // Set default weights
      const defaultWeights = {};
      metricsData.forEach((metric) => {
        defaultWeights[metric.key] = 1;
      });
      setWeights(defaultWeights);
    } catch (err) {
      setError('Failed to load initial data: ' + err.message);
    }
  }

  async function loadIndustriesForSectors(sectors) {
    try {
      // Load industries for all selected sectors
      const allIndustries = new Set();
      for (const sector of sectors) {
        const industriesData = await fetchIndustries(sector);
        industriesData.forEach((industry) => allIndustries.add(industry));
      }
      const industriesArray = Array.from(allIndustries);
      setIndustries(industriesArray);

      // Set default selection to all industries
      setSelectedIndustries(industriesArray);
    } catch (err) {
      console.error('Failed to load industries:', err);
    }
  }

  async function loadAllIndustries() {
    try {
      const industriesData = await fetchIndustries(); // No sector parameter = all industries
      setIndustries(industriesData);

      // Set default selection to all industries
      setSelectedIndustries(industriesData);
    } catch (err) {
      console.error('Failed to load all industries:', err);
    }
  }

  async function loadHeatmap() {
    setLoading(true);
    setError(null);

    try {
      // For multi-select, we need to fetch data for each selected sector/industry
      let allCompanies = [];

      if (selectedGroupBy === 'sector') {
        if (selectedSectors.length === 0) {
          // Load all sectors
          const data = await fetchHeatmapData({
            groupBy: 'sector',
            metrics: selectedMetrics,
            limit: 100,
          });
          allCompanies = data.companies || [];
        } else {
          // Load each selected sector
          for (const sector of selectedSectors) {
            const data = await fetchHeatmapData({
              groupBy: 'sector',
              groupName: sector,
              metrics: selectedMetrics,
              limit: 100,
            });
            allCompanies = allCompanies.concat(data.companies || []);
          }
        }
      } else if (selectedGroupBy === 'industry') {
        if (selectedIndustries.length === 0) {
          // Load all industries
          const data = await fetchHeatmapData({
            groupBy: 'industry',
            metrics: selectedMetrics,
            limit: 100,
          });
          allCompanies = data.companies || [];
        } else {
          // Load each selected industry
          for (const industry of selectedIndustries) {
            const data = await fetchHeatmapData({
              groupBy: 'industry',
              groupName: industry,
              metrics: selectedMetrics,
              limit: 100,
            });
            allCompanies = allCompanies.concat(data.companies || []);
          }
        }
      }

      // Filter companies based on selected sectors and industries
      let filteredCompanies = allCompanies;

      if (selectedSectors.length > 0) {
        filteredCompanies = filteredCompanies.filter((company) =>
          selectedSectors.includes(company.sector),
        );
      }

      if (selectedIndustries.length > 0) {
        filteredCompanies = filteredCompanies.filter((company) =>
          selectedIndustries.includes(company.industry),
        );
      }

      // Create heatmap data structure
      const data = {
        group:
          selectedGroupBy === 'sector'
            ? selectedSectors.length > 0
              ? selectedSectors.join(', ')
              : 'All Sectors'
            : selectedIndustries.length > 0
              ? selectedIndustries.join(', ')
              : 'All Industries',
        groupBy: selectedGroupBy,
        companies: filteredCompanies,
      };

      setHeatmapData(data);

      // Load price data for all companies
      console.log(
        ' About to load price data for companies:',
        filteredCompanies.length,
      );
      await loadPriceDataForCompanies(filteredCompanies);
    } catch (err) {
      setError('Failed to load heatmap: ' + err.message);
    } finally {
      setLoading(false);
    }
  }

  // Load price range data for companies
  async function loadPriceDataForCompanies(companies) {
    console.log(
      ' loadPriceDataForCompanies called with:',
      companies?.length || 0,
      'companies',
    );

    if (!companies || companies.length === 0) {
      console.log(' No companies to load price data for');
      return;
    }

    setIsLoadingPriceData(true);
    setPriceDataError(null);

    try {
      const symbols = companies.map((c) => c.symbol);
      console.log(
        ` Loading price data for ${symbols.length} companies:`,
        symbols.slice(0, 5),
      );

      const result = await loadPriceRangeData(symbols);
      console.log(' Price data result:', result);

      setPriceData(result.priceData);

      if (result.errors.length > 0) {
        console.warn(
          ` ${result.errors.length} companies failed to load price data:`,
          result.errors,
        );
        setPriceDataError(
          `${result.errors.length} companies failed to load price data`,
        );
      }

      console.log(
        ` Price data loaded: ${result.summary.loaded}/${result.summary.total} companies`,
      );
    } catch (error) {
      console.error(' Failed to load price data:', error);
      setPriceDataError('Failed to load price data: ' + error.message);
    } finally {
      setIsLoadingPriceData(false);
    }
  }

  async function applyFormula() {
    if (!heatmapData || !heatmapData.companies) {
      return;
    }

    try {
      let companiesToRank = heatmapData.companies;

      // Apply price range filters locally if we have price data
      console.log(' Checking price data and filters:', {
        hasPriceData: !!priceData,
        hasActiveFilters: hasActivePriceRangeFilters(),
        priceDataKeys: priceData ? Object.keys(priceData).length : 0,
        filters: priceRangeFilters,
      });

      if (priceData && hasActivePriceRangeFilters()) {
        console.log(' Applying local price range filters...');
        const filterResult = applyPriceRangeFilters(
          heatmapData.companies,
          priceData,
          priceRangeFilters,
        );
        companiesToRank = filterResult.passed;

        console.log(
          ` Local filtering: ${filterResult.summary.passed}/${filterResult.summary.total} companies passed`,
        );
        if (filterResult.summary.blacklisted > 0) {
          console.log(
            `Blacklisted: ${filterResult.summary.blacklisted} companies`,
          );
        }
      } else {
        console.log(
          ' Skipping price range filtering - no price data or no active filters',
        );
      }

      const formula = {
        method: formulaMethod,
        metrics: selectedMetrics,
        weights,
        threshold,
      };

      // Rank companies without price range filters (already applied locally)
      const result = await rankCompanies(companiesToRank, formula, null);
      setRankedCompanies(result.topCompanies);

      console.log(
        ` Ranking completed: ${result.topCompanies.length} companies ranked`,
      );
      console.log(
        ` Table will now show ${result.topCompanies.length} companies (filtered from ${heatmapData.companies.length})`,
      );
    } catch (err) {
      setError('Failed to rank companies: ' + err.message);
    }
  }

  // Real-time formula application with local filtering
  async function applyFormulaWithCurrentFilters() {
    if (!heatmapData || !heatmapData.companies) {
      return;
    }

    setIsFiltering(true);

    try {
      let companiesToRank = heatmapData.companies;

      // Apply price range filters locally if we have price data
      if (priceData && hasActivePriceRangeFilters()) {
        console.log(
          ' Applying real-time local price range filters:',
          priceRangeFilters,
        );
        const filterResult = applyPriceRangeFilters(
          heatmapData.companies,
          priceData,
          priceRangeFilters,
        );
        companiesToRank = filterResult.passed;

        console.log(
          ` Real-time local filtering: ${filterResult.summary.passed}/${filterResult.summary.total} companies passed`,
        );
        if (filterResult.summary.blacklisted > 0) {
          console.log(
            `Blacklisted: ${filterResult.summary.blacklisted} companies`,
          );
        }
      }

      const formula = {
        method: formulaMethod,
        metrics: selectedMetrics,
        weights,
        threshold,
      };

      // Rank companies without price range filters (already applied locally)
      const result = await rankCompanies(companiesToRank, formula, null);
      setRankedCompanies(result.topCompanies);

      console.log(
        ` Real-time ranking completed: ${result.topCompanies.length} companies ranked`,
      );
      console.log(
        ` Table will now show ${result.topCompanies.length} companies (filtered from ${heatmapData.companies.length})`,
      );
    } catch (err) {
      console.error('Failed to apply real-time filtering:', err.message);
      setError('Failed to apply real-time filtering: ' + err.message);
    } finally {
      setIsFiltering(false);
    }
  }

  function handleExportCSV() {
    if (heatmapData && heatmapData.companies) {
      exportToCSV(heatmapData.companies, selectedMetrics);
    }
  }

  function toggleMetric(metricKey) {
    if (selectedMetrics.includes(metricKey)) {
      setSelectedMetrics(selectedMetrics.filter((m) => m !== metricKey));
    } else {
      setSelectedMetrics([...selectedMetrics, metricKey]);
    }
  }

  // Price Range Filter helpers
  function updatePriceRangeFilter(period, minValue, maxValue) {
    console.log(
      ` Price range filter updated: ${period} = [${minValue}, ${maxValue}]`,
    );

    setPriceRangeFilters((prev) => {
      const newFilters = {
        ...prev,
        [period]: [minValue, maxValue],
      };
      console.log(' New price range filters:', newFilters);
      return newFilters;
    });

    // Debounced real-time filtering when price range filters change
    if (heatmapData && heatmapData.companies) {
      console.log(' Triggering debounced filtering...');
      // Clear existing timeout
      if (filterTimeout) {
        clearTimeout(filterTimeout);
      }

      // Set new timeout for debounced filtering
      const newTimeout = setTimeout(() => {
        console.log(' Debounce timeout triggered, applying filters...');
        applyFormulaWithCurrentFilters();
      }, 300); // 300ms debounce delay

      setFilterTimeout(newTimeout);
    } else {
      console.log(' Cannot trigger filtering - no heatmap data or companies');
    }
  }

  function resetPriceRangeFilters() {
    setPriceRangeFilters({
      '1D': [0, 100],
      '1W': [0, 100],
      '1M': [0, 100],
      '3M': [0, 100],
      '1Y': [0, 100],
    });

    // Immediate filtering when reset (no debounce for reset action)
    if (heatmapData && heatmapData.companies) {
      // Clear any pending debounced calls
      if (filterTimeout) {
        clearTimeout(filterTimeout);
        setFilterTimeout(null);
      }

      // Apply filters immediately
      applyFormulaWithCurrentFilters();
    }
  }

  function hasActivePriceRangeFilters() {
    const hasActive = Object.values(priceRangeFilters).some(
      ([min, max]) => min > 0 || max < 100,
    );
    console.log(' Checking for active price range filters:', {
      filters: priceRangeFilters,
      hasActive,
      details: Object.entries(priceRangeFilters).map(
        ([period, [min, max]]) => ({
          period,
          min,
          max,
          isActive: min > 0 || max < 100,
        }),
      ),
    });
    return hasActive;
  }

  return (
    <div className="flex h-screen bg-gray-50">
      {/* Left Panel - Filters & Formula Builder */}
      <div className="w-80 bg-white shadow-lg overflow-y-auto p-6">
        <h2 className="text-2xl font-bold mb-6"> Heat Map</h2>

        {/* Dimensions Section */}
        <div className="mb-6">
          <h3 className="text-lg font-semibold mb-4">Dimensions</h3>

          {/* Group By Selection */}
          <div className="mb-4">
            <Select
              label="Group By"
              value={selectedGroupBy}
              onChange={(e) => setSelectedGroupBy(e.target.value)}
              options={[
                { value: 'sector', label: 'Sector' },
                { value: 'industry', label: 'Industry' },
              ]}
              placeholder=""
            />
          </div>

          {/* Metrics Selection */}
          <div className="mb-4">
            <label className="block text-sm font-medium text-gray-700 mb-2">
              Metrics
            </label>
            <div className="space-y-3 max-h-96 overflow-y-auto">
              {/* Group metrics by category */}
              {['dividend', 'debt', 'leverage', 'profitability'].map(
                (category) => {
                  const categoryMetrics = availableMetrics.filter(
                    (m) => m.category === category,
                  );
                  if (categoryMetrics.length === 0) return null;

                  const categoryNames = {
                    dividend: 'Dividend',
                    debt: 'Debt',
                    leverage: 'Leverage',
                    profitability: 'Profitability',
                  };

                  return (
                    <div
                      key={category}
                      className="border-l-2 border-gray-300 pl-2"
                    >
                      <div className="text-xs font-semibold text-gray-600 uppercase mb-1">
                        {categoryNames[category]}
                      </div>
                      <div className="space-y-1">
                        {categoryMetrics.map((metric) => (
                          <label key={metric.key} className="flex items-center">
                            <input
                              type="checkbox"
                              checked={selectedMetrics.includes(metric.key)}
                              onChange={() => toggleMetric(metric.key)}
                              className="mr-2"
                            />
                            <span className="text-sm">{metric.label}</span>
                          </label>
                        ))}
                      </div>
                    </div>
                  );
                },
              )}
            </div>
          </div>

          <Button
            onClick={loadHeatmap}
            disabled={loading || selectedMetrics.length === 0}
            className="w-full"
          >
            {loading ? 'Loading...' : 'Load Heatmap'}
          </Button>
        </div>

        {/* Visual Separator */}
        {heatmapData && <hr className="my-6 border-gray-200" />}

        {/* Filters Section - Only show after heatmap is loaded */}
        {heatmapData && (
          <div className="mb-6">
            <h3 className="text-lg font-semibold mb-4">Filters</h3>

            {/* Sectors Multi-Select */}
            <CheckboxList
              items={sectors}
              selectedItems={selectedSectors}
              onChange={setSelectedSectors}
              label="Sectors"
              searchPlaceholder="Search sectors..."
              className="mb-4"
            />

            {/* Industries Multi-Select */}
            <CheckboxList
              items={industries}
              selectedItems={selectedIndustries}
              onChange={setSelectedIndustries}
              label="Industries"
              searchPlaceholder="Search industries..."
              className="mb-4"
            />
          </div>
        )}

        {/* Price Range Filters Section - Only show after heatmap is loaded */}
        {heatmapData && (
          <div className="mb-4">
            <div className="flex items-center justify-between mb-2">
              <label className="block text-sm font-medium text-gray-700">
                Price Range Filters
                {isFiltering && (
                  <span className="ml-2 text-xs text-blue-600">
                     Filtering...
                  </span>
                )}
              </label>
              <button
                onClick={resetPriceRangeFilters}
                className="text-xs text-blue-600 hover:text-blue-800"
                disabled={isFiltering}
              >
                Reset All
              </button>
            </div>

            <div className="space-y-4">
              {Object.entries(priceRangeFilters).map(
                ([period, [minValue, maxValue]]) => (
                  <DualSlider
                    key={period}
                    label={`${period} Range`}
                    value={[minValue, maxValue]}
                    onChange={(newValue) =>
                      updatePriceRangeFilter(period, newValue[0], newValue[1])
                    }
                    min={0}
                    max={100}
                    step={1}
                    formatValue={(value) => `${value}%`}
                    className="border border-gray-200 rounded-lg p-3"
                  />
                ),
              )}
            </div>

            {hasActivePriceRangeFilters() && (
              <div className="mt-3 text-xs text-blue-600">
                 Active filters will exclude stocks outside the specified
                ranges
                {priceData && (
                  <span className="ml-1">
                    (Local filtering active - {Object.keys(priceData).length}{' '}
                    stocks loaded)
                  </span>
                )}
              </div>
            )}

            <div className="mt-2 text-xs text-gray-500">
               Filters apply automatically as you adjust the sliders
            </div>

            <div className="mt-1 text-xs text-orange-600">
               Using mock filtering (set EODHD_API_KEY for real data)
            </div>

            {isLoadingPriceData && (
              <div className="mt-2 text-xs text-blue-600">
                 Loading price data...
              </div>
            )}

            {priceDataError && (
              <div className="mt-2 text-xs text-red-600">
                 {priceDataError}
              </div>
            )}

            {priceData && (
              <div className="mt-2 text-xs text-green-600">
                 Price data loaded for {Object.keys(priceData).length} stocks
              </div>
            )}

            {!priceData && !heatmapData && (
              <div className="mt-2 text-xs text-blue-600">
                 Click &ldquo;Load Heatmap&rdquo; to load price data and
                enable filtering
              </div>
            )}

            {!priceData && heatmapData && (
              <div className="mt-2 text-xs text-orange-600">
                 Price data not loaded - filtering will not work. Click
                &ldquo;Load Heatmap&rdquo; again.
              </div>
            )}
          </div>
        )}

        {/* Visual Separator */}
        {heatmapData && <hr className="my-6 border-gray-200" />}

        {/* Optimum Section - Only show after heatmap is loaded */}
        {heatmapData && (
          <div className="mb-6">
            <h3 className="text-lg font-semibold mb-4">Optimum</h3>

            <div className="mb-4">
              <Select
                label="Ranking Method"
                value={formulaMethod}
                onChange={(e) => setFormulaMethod(e.target.value)}
                options={[
                  { value: 'avg', label: 'Average Percentile' },
                  { value: 'weighted', label: 'Weighted Average' },
                  { value: 'count', label: 'Count  Threshold' },
                ]}
                placeholder=""
              />
            </div>

            {/* Weights (for weighted method) */}
            {formulaMethod === 'weighted' && (
              <div className="mb-4">
                <label className="block text-sm font-medium text-gray-700 mb-2">
                  Metric Weights
                </label>
                {selectedMetrics.map((metricKey) => {
                  const metric = availableMetrics.find(
                    (m) => m.key === metricKey,
                  );
                  return (
                    <Slider
                      key={metricKey}
                      label={metric?.label}
                      value={weights[metricKey] || 1}
                      onChange={(value) =>
                        setWeights({ ...weights, [metricKey]: value })
                      }
                      min={0}
                      max={5}
                      step={0.5}
                      className="mb-3"
                    />
                  );
                })}
              </div>
            )}

            {/* Threshold */}
            <Slider
              label={`High Percentile Threshold: ${Math.round(threshold * 100)}%`}
              value={threshold}
              onChange={setThreshold}
              min={0}
              max={1}
              step={0.05}
              formatValue={(value) => `${Math.round(value * 100)}%`}
              className="mb-4"
            />

            <Button
              onClick={applyFormula}
              disabled={!heatmapData}
              variant="success"
              className="w-full"
            >
              Apply Formula
            </Button>
          </div>
        )}
      </div>

      {/* Main Content */}
      <div className="flex-1 overflow-auto p-6">
        {error && (
          <div className="bg-red-50 border border-red-200 text-red-700 px-4 py-3 rounded mb-4">
            {error}
          </div>
        )}

        {/* Top Companies Panel */}
        {rankedCompanies.length > 0 && (
          <div className="mb-6 bg-white rounded-lg shadow p-6">
            <h3 className="text-xl font-bold mb-4"> Top Companies</h3>
            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
              {rankedCompanies.slice(0, 6).map((company) => (
                <div
                  key={company.symbol}
                  className="border border-gray-200 rounded-lg p-4"
                >
                  <div className="font-bold text-lg">{company.symbol}</div>
                  <div className="text-sm text-gray-600">{company.sector}</div>
                  <div className="mt-2">
                    <div className="text-sm">
                      Score:{' '}
                      <span className="font-semibold">
                        {(company.score * 100).toFixed(1)}%
                      </span>
                    </div>
                    <div className="text-sm">
                      High Metrics:{' '}
                      <span className="font-semibold">{company.highCount}</span>
                    </div>
                  </div>
                </div>
              ))}
            </div>
          </div>
        )}

        {/* Heatmap Table */}
        {heatmapData && (
          <>
            <div className="flex justify-between items-center mb-4">
              <div>
                <h3 className="text-xl font-bold">
                  {heatmapData.group} -{' '}
                  {rankedCompanies.length > 0
                    ? rankedCompanies.length
                    : heatmapData.companies.length}{' '}
                  Companies
                  {rankedCompanies.length > 0 &&
                    rankedCompanies.length !== heatmapData.companies.length && (
                      <span className="text-sm text-blue-600 ml-2">
                        (Filtered from {heatmapData.companies.length})
                      </span>
                    )}
                </h3>
                {rankedCompanies.length > 0 &&
                  rankedCompanies.length !== heatmapData.companies.length && (
                    <div className="text-sm text-blue-600 mt-1">
                       Price range filters active - showing filtered results
                    </div>
                  )}
              </div>
              <Button onClick={handleExportCSV} variant="secondary">
                 Export CSV
              </Button>
            </div>

            <Table
              columns={[
                {
                  key: 'symbol',
                  label: 'Symbol',
                  align: 'left',
                },
                {
                  key: 'group',
                  label: selectedGroupBy === 'sector' ? 'Sector' : 'Industry',
                  align: 'left',
                },
                ...selectedMetrics.map((metricKey) => {
                  const metric = availableMetrics.find(
                    (m) => m.key === metricKey,
                  );
                  return {
                    key: metricKey,
                    label: metric?.label,
                    align: 'center',
                    render: (value) => {
                      if (value === null || value === undefined) return '-';
                      // Display percentile as percentage (0-100%)
                      return (value * 100).toFixed(0) + '%';
                    },
                    getTooltip: (row) => {
                      const percentile = row.percentiles[metricKey];
                      const raw = row.raw[metricKey];
                      const metricType = metric?.type || 'unknown';

                      const rawFormatted =
                        raw !== null && raw !== undefined
                          ? formatMetricValue(raw, metricType)
                          : 'N/A';
                      const percentileFormatted =
                        percentile !== null && percentile !== undefined
                          ? (percentile * 100).toFixed(1) + '%'
                          : 'N/A';

                      return `Raw Value: ${rawFormatted}\nPercentile: ${percentileFormatted}\nThreshold: ${(threshold * 100).toFixed(0)}%`;
                    },
                  };
                }),
              ]}
              data={(rankedCompanies.length > 0
                ? rankedCompanies
                : heatmapData.companies
              ).map((company) => ({
                id: company.symbol,
                symbol: company.symbol,
                group:
                  selectedGroupBy === 'sector'
                    ? company.sector
                    : company.industry,
                ...company,
              }))}
              getCellValue={(row, column) => {
                if (column.key === 'symbol' || column.key === 'group') {
                  return row[column.key];
                }
                return row.percentiles[column.key];
              }}
              getCellClassName={(row, column, value) => {
                if (column.key === 'symbol' || column.key === 'group') {
                  return column.key === 'group' ? 'text-gray-600' : '';
                }
                const percentile = value;
                const colorClass = getPercentileColor(percentile, threshold);
                const textClass = getTextColorForBg(percentile);
                return `${colorClass} ${textClass}`;
              }}
              stickyFirstColumn={true}
            />
          </>
        )}

        {!heatmapData && !loading && (
          <div className="text-center text-gray-500 mt-20">
            <div className="text-6xl mb-4"></div>
            <div className="text-xl">
              Select filters and click &ldquo;Load Heatmap&rdquo; to view data
            </div>
          </div>
        )}
      </div>
    </div>
  );
}

export default HeatMap;
