import { useState, useEffect } from 'react';
import {
  BarChart3,
  Clock,
  CheckCircle,
  XCircle,
  Database,
  RefreshCw,
  AlertCircle,
} from 'lucide-react';
import { eodhdUsageApi } from '../services/eodhdUsageApi';
import PieChart from '../components/PieChart';

const EodhdUsage = () => {
  const [stats, setStats] = useState(null);
  const [endpointStats, setEndpointStats] = useState([]);
  const [recentUsage, setRecentUsage] = useState([]);
  const [availableEndpoints, setAvailableEndpoints] = useState([]);
  const [availableJobs, setAvailableJobs] = useState([]);
  const [endpointTypes, setEndpointTypes] = useState([]);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);

  // Filters
  const [endpointFilter, setEndpointFilter] = useState('all');
  const [jobFilter, setJobFilter] = useState('all');
  const [dateRange, setDateRange] = useState('24h');
  const [limit, setLimit] = useState(100);

  // Load data
  const loadData = async () => {
    setLoading(true);
    setError(null);

    try {
      // Build filters
      const filters = {};
      if (endpointFilter !== 'all') filters.endpoint = endpointFilter;
      if (jobFilter !== 'all') filters.jobName = jobFilter;
      if (dateRange !== 'all') {
        const since = getDateRangeFilter(dateRange);
        if (since) filters.since = since.toISOString();
      }

      // Load all data in parallel
      const [
        statsData,
        endpointStatsData,
        recentUsageData,
        endpointsData,
        jobsData,
        endpointTypesData,
      ] = await Promise.all([
        eodhdUsageApi.getStats(filters),
        eodhdUsageApi.getEndpointStats(filters),
        eodhdUsageApi.getRecentUsage({ ...filters, limit }),
        eodhdUsageApi.getAvailableEndpoints(),
        eodhdUsageApi.getAvailableJobs(),
        eodhdUsageApi.getEndpointTypes(),
      ]);

      setStats(statsData.stats);
      setEndpointStats(endpointStatsData.endpointStats);
      setRecentUsage(recentUsageData.usage);
      setAvailableEndpoints(endpointsData.endpoints);
      setAvailableJobs(jobsData.jobs);
      setEndpointTypes(endpointTypesData.endpointTypes);
    } catch (err) {
      setError(err.message);
      console.error('Failed to load EODHD usage data:', err);
    } finally {
      setLoading(false);
    }
  };

  // Load data on component mount and when filters change
  useEffect(() => {
    loadData();
  }, [endpointFilter, jobFilter, dateRange, limit]);

  // Auto-refresh every 30 seconds
  useEffect(() => {
    const interval = setInterval(loadData, 30000);
    return () => clearInterval(interval);
  }, [endpointFilter, jobFilter, dateRange, limit]);

  const getDateRangeFilter = (range) => {
    const now = new Date();
    switch (range) {
      case '1h':
        return new Date(now.getTime() - 60 * 60 * 1000);
      case '6h':
        return new Date(now.getTime() - 6 * 60 * 60 * 1000);
      case '24h':
        return new Date(now.getTime() - 24 * 60 * 60 * 1000);
      case '7d':
        return new Date(now.getTime() - 7 * 24 * 60 * 60 * 1000);
      case '30d':
        return new Date(now.getTime() - 30 * 24 * 60 * 60 * 1000);
      default:
        return null;
    }
  };

  const formatDate = (dateString) => {
    if (!dateString) return 'N/A';
    return new Date(dateString).toLocaleString();
  };

  const formatTimeAgo = (dateString) => {
    if (!dateString) return 'Unknown';
    const date = new Date(dateString);
    const now = new Date();
    const diffMs = now - date;
    const diffMins = Math.floor(diffMs / 60000);
    const diffHours = Math.floor(diffMins / 60);
    const diffDays = Math.floor(diffHours / 24);

    if (diffMins < 1) return 'Just now';
    if (diffMins < 60) return `${diffMins}m ago`;
    if (diffHours < 24) return `${diffHours}h ago`;
    return `${diffDays}d ago`;
  };

  const formatResponseTime = (ms) => {
    if (ms < 1000) return `${Math.round(ms)}ms`;
    return `${(ms / 1000).toFixed(1)}s`;
  };

  const getStatusColor = (isSuccess) => {
    return isSuccess
      ? 'text-green-600 bg-green-100'
      : 'text-red-600 bg-red-100';
  };

  const getStatusIcon = (isSuccess) => {
    return isSuccess ? CheckCircle : XCircle;
  };

  // Prepare data for pie charts
  const preparePieChartData = (data, valueKey, labelKey = 'endpoint') => {
    return data
      .map((item) => ({
        label: item[labelKey],
        value: item[valueKey] || 0,
      }))
      .filter((item) => item.value > 0);
  };

  // Get pie chart data for different metrics
  const getRequestsPieData = () =>
    preparePieChartData(endpointStats, 'totalRequests');
  const getCacheHitsPieData = () =>
    preparePieChartData(endpointStats, 'cachedRequests');
  const getSuccessPieData = () =>
    preparePieChartData(endpointStats, 'successfulRequests');
  const getFailedPieData = () =>
    preparePieChartData(endpointStats, 'failedRequests');

  return (
    <div className="space-y-6">
      {/* Header */}
      <div className="flex items-center justify-between">
        <div>
          <h1 className="text-2xl font-bold text-gray-900">EODHD API Usage</h1>
          <p className="text-gray-600">
            Monitor and analyze EODHD API requests and performance
          </p>
        </div>
        <button onClick={loadData} disabled={loading} className="btn-secondary">
          <RefreshCw
            size={16}
            className={`mr-2 ${loading ? 'animate-spin' : ''}`}
          />
          Refresh
        </button>
      </div>

      {/* Filters */}
      <div className="card p-6">
        <div className="flex flex-col lg:flex-row gap-4">
          <div className="flex gap-4">
            <div className="w-48">
              <select
                value={endpointFilter}
                onChange={(e) => setEndpointFilter(e.target.value)}
                className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-primary-500 focus:border-transparent"
              >
                <option value="all">All Endpoints</option>
                {availableEndpoints.map((endpoint) => (
                  <option key={endpoint} value={endpoint}>
                    {endpoint}
                  </option>
                ))}
              </select>
            </div>

            <div className="w-48">
              <select
                value={jobFilter}
                onChange={(e) => setJobFilter(e.target.value)}
                className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-primary-500 focus:border-transparent"
              >
                <option value="all">All Jobs</option>
                {availableJobs.map((job) => (
                  <option key={job} value={job}>
                    {job}
                  </option>
                ))}
              </select>
            </div>

            <div className="w-32">
              <select
                value={dateRange}
                onChange={(e) => setDateRange(e.target.value)}
                className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-primary-500 focus:border-transparent"
              >
                <option value="1h">Last Hour</option>
                <option value="6h">Last 6 Hours</option>
                <option value="24h">Last 24 Hours</option>
                <option value="7d">Last 7 Days</option>
                <option value="30d">Last 30 Days</option>
                <option value="all">All Time</option>
              </select>
            </div>

            <div className="w-32">
              <select
                value={limit}
                onChange={(e) => setLimit(Number(e.target.value))}
                className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:ring-2 focus:ring-primary-500 focus:border-transparent"
              >
                <option value={50}>50 results</option>
                <option value={100}>100 results</option>
                <option value={200}>200 results</option>
                <option value={500}>500 results</option>
              </select>
            </div>
          </div>
        </div>
      </div>

      {/* Error State */}
      {error && (
        <div className="bg-red-50 border border-red-200 rounded-lg p-4">
          <div className="flex items-center">
            <AlertCircle className="text-red-600 mr-2" size={20} />
            <div>
              <h3 className="text-sm font-medium text-red-800">API Error</h3>
              <p className="text-sm text-red-600 mt-1">{error}</p>
            </div>
          </div>
        </div>
      )}

      {/* Statistics Cards */}
      {stats && (
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-4">
          <div className="card p-4">
            <div className="flex items-center justify-between">
              <div>
                <p className="text-sm font-medium text-gray-600">
                  Total Requests
                </p>
                <p className="text-xl font-bold text-gray-900">
                  {stats.totalRequests?.toLocaleString() || 0}
                </p>
              </div>
              <BarChart3 className="text-gray-400" size={20} />
            </div>
          </div>

          <div className="card p-4">
            <div className="flex items-center justify-between">
              <div>
                <p className="text-sm font-medium text-gray-600">
                  Cache Hit Rate
                </p>
                <p className="text-xl font-bold text-green-600">
                  {stats.cacheHitRate?.toFixed(1) || 0}%
                </p>
              </div>
              <Database className="text-green-400" size={20} />
            </div>
          </div>

          <div className="card p-4">
            <div className="flex items-center justify-between">
              <div>
                <p className="text-sm font-medium text-gray-600">
                  Success Rate
                </p>
                <p className="text-xl font-bold text-blue-600">
                  {stats.successRate?.toFixed(1) || 0}%
                </p>
              </div>
              <CheckCircle className="text-blue-400" size={20} />
            </div>
          </div>

          <div className="card p-4">
            <div className="flex items-center justify-between">
              <div>
                <p className="text-sm font-medium text-gray-600">
                  Avg Response Time
                </p>
                <p className="text-xl font-bold text-purple-600">
                  {formatResponseTime(stats.avgResponseTime || 0)}
                </p>
              </div>
              <Clock className="text-purple-400" size={20} />
            </div>
          </div>
        </div>
      )}

      {/* Pie Charts for Endpoint Metrics */}
      {endpointStats.length > 0 && (
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6">
          <div className="card p-6">
            <PieChart
              data={getRequestsPieData()}
              title="Requests by Endpoint"
              colors={[
                '#3B82F6',
                '#10B981',
                '#F59E0B',
                '#EF4444',
                '#8B5CF6',
                '#06B6D4',
              ]}
            />
          </div>

          <div className="card p-6">
            <PieChart
              data={getCacheHitsPieData()}
              title="Cache Hits by Endpoint"
              colors={[
                '#10B981',
                '#059669',
                '#047857',
                '#065F46',
                '#064E3B',
                '#022C22',
              ]}
            />
          </div>

          <div className="card p-6">
            <PieChart
              data={getSuccessPieData()}
              title="Successful Requests by Endpoint"
              colors={[
                '#3B82F6',
                '#2563EB',
                '#1D4ED8',
                '#1E40AF',
                '#1E3A8A',
                '#172554',
              ]}
            />
          </div>

          <div className="card p-6">
            <PieChart
              data={getFailedPieData()}
              title="Failed Requests by Endpoint"
              colors={[
                '#EF4444',
                '#DC2626',
                '#B91C1C',
                '#991B1B',
                '#7F1D1D',
                '#450A0A',
              ]}
            />
          </div>
        </div>
      )}

      {/* Endpoint Statistics */}
      <div className="card">
        <div className="px-6 py-4 border-b border-gray-200">
          <h3 className="text-lg font-medium text-gray-900">
            Endpoint Performance
          </h3>
          <p className="text-sm text-gray-600 mt-1">
            All available EODHD API endpoints with usage statistics
          </p>
        </div>
        <div className="overflow-x-auto">
          <table className="min-w-full divide-y divide-gray-200">
            <thead className="bg-gray-50">
              <tr>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                  Endpoint
                </th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                  Category
                </th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                  Requests
                </th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                  Cache Hit Rate
                </th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                  Success Rate
                </th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                  Avg Response Time
                </th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                  Last Request
                </th>
              </tr>
            </thead>
            <tbody className="bg-white divide-y divide-gray-200">
              {endpointTypes.map((endpointType) => {
                // Find matching stats for this endpoint
                const stats = endpointStats.find(
                  (s) => s.endpoint === endpointType.displayName,
                );

                return (
                  <tr key={endpointType.id} className="hover:bg-gray-50">
                    <td className="px-6 py-4 whitespace-nowrap text-sm font-medium text-gray-900">
                      <div>
                        <div>{endpointType.displayName}</div>
                        <div className="text-xs text-gray-500">
                          {endpointType.description}
                        </div>
                      </div>
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">
                      <span className="px-2 py-1 text-xs font-medium rounded-full bg-gray-100 text-gray-800">
                        {endpointType.category}
                      </span>
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">
                      {stats?.totalRequests?.toLocaleString() || 0}
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">
                      <span
                        className={`px-2 py-1 text-xs font-medium rounded-full ${
                          (stats?.cacheHitRate || 0) > 50
                            ? 'text-green-600 bg-green-100'
                            : 'text-yellow-600 bg-yellow-100'
                        }`}
                      >
                        {stats?.cacheHitRate?.toFixed(1) || 0}%
                      </span>
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">
                      <span
                        className={`px-2 py-1 text-xs font-medium rounded-full ${
                          (stats?.successRate || 0) > 90
                            ? 'text-green-600 bg-green-100'
                            : 'text-red-600 bg-red-100'
                        }`}
                      >
                        {stats?.successRate?.toFixed(1) || 0}%
                      </span>
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">
                      {formatResponseTime(stats?.avgResponseTime || 0)}
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">
                      {stats?.lastRequest
                        ? formatTimeAgo(stats.lastRequest)
                        : 'Never'}
                    </td>
                  </tr>
                );
              })}
            </tbody>
          </table>
        </div>
      </div>

      {/* Recent Usage Table */}
      <div className="card">
        <div className="px-6 py-4 border-b border-gray-200">
          <h3 className="text-lg font-medium text-gray-900">
            Recent API Requests
          </h3>
        </div>
        <div className="overflow-x-auto">
          <table className="min-w-full divide-y divide-gray-200">
            <thead className="bg-gray-50">
              <tr>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                  Time
                </th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                  Endpoint
                </th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                  Status
                </th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                  Response Time
                </th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                  Cached
                </th>
                <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                  Job
                </th>
              </tr>
            </thead>
            <tbody className="bg-white divide-y divide-gray-200">
              {loading ? (
                <tr>
                  <td colSpan="6" className="px-6 py-12 text-center">
                    <div className="flex items-center justify-center">
                      <RefreshCw
                        className="animate-spin text-gray-400 mr-2"
                        size={20}
                      />
                      <span className="text-gray-600">
                        Loading usage data...
                      </span>
                    </div>
                  </td>
                </tr>
              ) : recentUsage.length === 0 ? (
                <tr>
                  <td colSpan="6" className="px-6 py-12 text-center">
                    <div className="flex flex-col items-center">
                      <AlertCircle className="text-gray-400 mb-2" size={32} />
                      <h3 className="text-sm font-medium text-gray-900">
                        No usage data found
                      </h3>
                      <p className="text-sm text-gray-500 mt-1">
                        Try adjusting your filter criteria or check back later.
                      </p>
                    </div>
                  </td>
                </tr>
              ) : (
                recentUsage.map((usage) => {
                  const StatusIcon = getStatusIcon(usage.isSuccess);

                  return (
                    <tr key={usage._id} className="hover:bg-gray-50">
                      <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">
                        <div>
                          <div>{formatTimeAgo(usage.requestedAt)}</div>
                          <div className="text-xs text-gray-400">
                            {formatDate(usage.requestedAt)}
                          </div>
                        </div>
                      </td>
                      <td className="px-6 py-4 whitespace-nowrap text-sm font-medium text-gray-900">
                        {usage.endpoint}
                      </td>
                      <td className="px-6 py-4 whitespace-nowrap">
                        <span
                          className={`inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium ${getStatusColor(usage.isSuccess)}`}
                        >
                          <StatusIcon size={12} className="mr-1" />
                          {usage.statusCode}
                        </span>
                      </td>
                      <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">
                        {formatResponseTime(usage.responseTime)}
                      </td>
                      <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">
                        {usage.isCached ? (
                          <span className="inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium text-green-600 bg-green-100">
                            <Database size={12} className="mr-1" />
                            Cached
                          </span>
                        ) : (
                          <span className="text-gray-400">-</span>
                        )}
                      </td>
                      <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">
                        {usage.jobName || '-'}
                      </td>
                    </tr>
                  );
                })
              )}
            </tbody>
          </table>
        </div>
      </div>

      {/* Results Summary */}
      {recentUsage.length > 0 && (
        <div className="text-center text-sm text-gray-500">
          Showing {recentUsage.length} of {stats?.totalRequests || 0} API
          requests
          {endpointFilter !== 'all' &&
            ` (filtered by endpoint: ${endpointFilter})`}
          {jobFilter !== 'all' && ` (filtered by job: ${jobFilter})`}
          {dateRange !== 'all' && ` (filtered by time: ${dateRange})`}
        </div>
      )}
    </div>
  );
};

export default EodhdUsage;
